.navigation-control {
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  color: #fff;
  opacity: 0.5;
  transition: all 0.3s linear 0s;
  display: none;
  outline: none; }
  .navigation-control:after {
    display: block;
    font-size: 80px;
    padding: 0 0.1em; }
  .navigation-control:hover {
    opacity: 1; }
  @media (min-width: 768px) {
    .navigation-control.active {
      display: block; } }
  .navigation-control.left {
    left: 0; }
    .navigation-control.left:after {
      content: "⟨"; }
  .navigation-control.right {
    right: 0; }
    .navigation-control.right:after {
      content: "⟩"; }
